import InternetPackage from './InternetPackage';
import diemaExtraIcon from '../../images/checkout/extra-1.png';
import movieStarIcon from '../../images/checkout/extra-2.png';

class TvPackage extends InternetPackage {
  constructor(id, size, speed, price, features, title, hdFetcherInfo, tvCount) {
    super(id, size, speed, price, features);
  
    this.title = title;
    this.tvChannels = title.split(" ")[0] + "+";
    this.hdChannels = Math.floor(parseInt(title.split(" ")[0]) / 3) + "+";
    this.hdFetcher = tvCount;
    this.hdFetcherInfo = "     1 IPTV  (STB).    ,       IPTV . IPTV    ,           .";
    this.isOnlyInternet = false;

    this.tvExtras = [
      { extra: new TvExtra('Diema Extra', '         Diema Extra!', diemaExtraIcon, 8.99), isAdded: false },
      { extra: new TvExtra('MovieSTAR', '          ,       .', movieStarIcon, 2.99), isAdded: false },
    ];
  }

  setExtraAdded = (index, isAdd) => {
    this.tvExtras[index] = { ...this.tvExtras[index], isAdded: isAdd };
  }

  setTvCount = (tvCount) => this.hdFetcher = tvCount;

  getPriceWithExtras = () => {
    return (this.getPriceAccordingToDuration() + this.getAdditionalTVsPrice(this.hdFetcher) + this.getExtrasPrice());
  }

  getAdditionalTVsPrice = (numberOfTvs) => {
    const tvNum = parseInt(numberOfTvs);
    switch (tvNum) {
      case 1:
        return 0;
      case 2:
      case 3:
        return (tvNum - 1) * 2.5;
      default:
        return (tvNum - 3) * 5 + 5;
    }
  }

  getExtrasPrice = () => {
    let res = 0;
    this.tvExtras.forEach((v) => {
      if (v.isAdded) res += v.extra.price;
    });
    return res;
  };

  toStringTvExtras = () => {
    let res = '';
    this.tvExtras.forEach((v) => {
      if(v.isAdded){
        res += v.extra.name + " +" + v.extra.price + "; ";
      }
    });
    return res;
  }

  toString = () => {
    return ": " + this.internalTitle
      + "\n<br>: " + this.title
      + "\n<br> : " + this.internetSpeed
      + "\n<br>  : " + this.duration + "."
      + "\n<br> : " + String(this.originalPrice).split('.')[0] + "." + String(this.originalPrice).split('.')[1].slice(0, 2)
      + "\n<br>  : " + this.hdFetcher
      + "\n<br>: " + this.toStringTvExtras()
      + "\n<br>  : " + this.getPriceMain() + "." + this.getPriceCoins();
  }
}

class TvExtra {
  constructor(name, description, icon, price) {
    this.name = name;
    this.description = description;
    this.icon = icon;
    this.price = price;
  }
}

Object.setPrototypeOf(TvPackage.prototype, InternetPackage);

export default TvPackage;