import React from 'react';
import DatePicker from 'react-date-picker';
import { validateField } from '../shared/form-popup';

export const StepForm = ({ dispatch, parentState }) => {
  const { firstName,
    firstNameIsValid,
    secondName,
    secondNameIsValid,
    telephone,
    telephoneIsValid,
    email,
    emailIsValid,
    city,
    cityIsValid,
    address,
    addressIsValid,
    selectedDate,
    selectedDateIsValid,
    agreeCheckbox,
    packageInfo,
    failure } = parentState;

  const createBody = () => {
    const packageString = packageInfo.toString();

    const user = "\n<br> : "
      + "\n<br>: " + firstName + " " + secondName
      + "\n<br>: " + telephone
      + "\n<br>: " + email
      + "\n<br>: " + city
      + "\n<br>: " + address
      + "\n<br>   .: " + formatDate(selectedDate);

    return user + "\n<br>---------------\n<br>" + packageString;

  }

  const onSubmit = (e) => {
    e.preventDefault();

    if (checkFields()) {
      const send_to = 'callcenter@netsurf.bg';
      const subject = '    ' + firstName + ' ' + secondName;
      const body = createBody();
      const reply_to = email;

      const emailObj = {
        send_to: send_to,
        subject: subject,
        body: body,
        reply_to: reply_to
      };

      const headers = new Headers();
      headers.append('Content-Type', 'application/json');

      const POSToptions = {
        method: 'POST',
        headers: headers,
        body: JSON.stringify(emailObj)
      }

      const url = 'http://localhost:3000/contact';
      const request = new Request(url, POSToptions);

      fetch(request)
        .then((_) => {
          dispatch({ type: 'next_step' });
        })
        .catch(e => {
          dispatch({ type: 'failure' });
        });
    }
  }

  const checkFields = () => {
    const fn = validateField('name', firstName);
    const sn = validateField('name', secondName);
    const e = validateField('email', email);
    const t = validateField('telephone', telephone);
    const c = validateField('city', city);
    const a = validateField('address', address);
    const d = validateField('date', selectedDate);

    dispatch({
      type: 'valid_fields',
      'firstNameIsValid': fn,
      'secondNameIsValid': sn,
      'emailIsValid': e,
      'telephoneIsValid': t,
      'cityIsValid': c,
      'addressIsValid': a,
      'selectedDateIsValid': d,
    });

    return fn && sn && e && t && c && a && d && agreeCheckbox;
  }



  return (<>
    <h2 className='mb-5'> </h2>
    <div className='row'>
      <form onSubmit={onSubmit} className='popup-form'>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label for="firstName">  </label>
            <input type="text" className="form-control" id="firstName" placeholder="e.g. " value={firstName} onChange={(v) => dispatch({
              type: 'form_field',
              field: 'firstName',
              fieldValid: 'firstNameIsValid',
              value: v.currentTarget.value,
            })} />
            {!firstNameIsValid && <small className='text-danger'> 3 </small>}
          </div>

          <div className="form-group col-md-6">
            <label for="secondName">  </label>
            <input type="text" className="form-control" id="secondName" placeholder="e.g. " value={secondName} onChange={(v) => dispatch({
              type: 'form_field',
              field: 'secondName',
              fieldValid: 'secondNameIsValid',
              value: v.currentTarget.value,
            })} />
            {!secondNameIsValid && <small className='text-danger'> 3 </small>}
          </div>

          <div className="form-group col-md-6">
            <label for="telephone"></label>
            <input type="text" className="form-control" id="telephone" placeholder=" " value={telephone} onChange={(v) => dispatch({
              type: 'form_field',
              field: 'telephone',
              fieldValid: 'telephoneIsValid',
              value: v.currentTarget.value,
            })} />
            {!telephoneIsValid && <small className='text-danger'> </small>}
          </div>

          <div className="form-group col-md-6">
            <label for="email"></label>
            <input type="text" className="form-control" id="email" placeholder=" " value={email} onChange={(v) => dispatch({
              type: 'form_field',
              field: 'email',
              fieldValid: 'emailIsValid',
              value: v.currentTarget.value,
            })} />
            {!emailIsValid && <small className='text-danger'> </small>}
          </div>

          <div className="form-group col-md-4">
            <label for="city"></label>
            <input type="text" className="form-control" id="city" placeholder="" value={city} onChange={(v) => dispatch({
              type: 'form_field',
              field: 'city',
              fieldValid: 'cityIsValid',
              value: v.currentTarget.value,
            })} />
            {!cityIsValid && <small className='text-danger'> 3 </small>}
          </div>

          <div className="form-group col-md-8">
            <label for="address"></label>
            <input type="text" className="form-control" id="address" placeholder=", , , , , , " value={address} onChange={(v) => dispatch({
              type: 'form_field',
              field: 'address',
              fieldValid: 'addressIsValid',
              value: v.currentTarget.value,
            })} />
            {!addressIsValid && <small className='text-danger'> 3 </small>}
          </div>

          <div className="form-group col-md-6">
            <label for="text">   </label>
            <DatePicker
              className={'form-group'}
              // selected={new Date()}
              minDate={new Date()}
              value={selectedDate}
              onChange={(v) => dispatch({
                type: 'form_field',
                field: 'selectedDate',
                value: v,
              })}
              clearIcon={null}
            />
            {!selectedDateIsValid && <small className='text-danger'> </small>}
          </div>
        </div>

        <button type="submit" className='btn btn-primary green mt-5'></button>
        <div class="form-check">
          <input type="checkbox" class="form-check-input" id="agreeCheckbox"
            value={agreeCheckbox}
            onChange={(v) => {
              dispatch({
                type: 'form_field',
                field: 'agreeCheckbox',
                value: !agreeCheckbox,
              })
            }}
          />
          <label class="form-check-label" for="agreeCheckbox">        GDPR</label>
        </div>
        {failure && <p className='text-danger'>  !  .</p>}
      </form>
    </div>


  </>);
};

function formatDate(date) {
  var monthNames = [
    "January", "February", "March",
    "April", "May", "June", "July",
    "August", "September", "October",
    "November", "December"
  ];

  var day = date.getDate();
  var monthIndex = date.getMonth();
  var year = date.getFullYear();

  return day + ' ' + monthNames[monthIndex] + ' ' + year;
}
